@mixin center-circle {
  width: $clock-center-height;
  height: $clock-center-height;
}

@mixin container {
  width: $container-width;
  height: $container-width;
}

@mixin arrow-constructor {
  position: absolute;
  left: 148px;
  border-radius: 5px;
  transform-origin: $transform-origin;
}
